.container {
  display: flex;
  justify-content: space-between;
  height: 100vh;
  overflow: hidden;
  width: 90%;
  margin-left: auto;
  margin-right: auto; }

.logo {
  font-size: 1.25rem;
  margin-bottom: 0;
  display: flex;
  justify-content: center; }
  .logo > a {
    color: inherit; }

.logo-subtitle {
  font-size: 0.75rem; }

.sidebar {
  color: #21262c;
  min-width: 16rem;
  background-color: #f1f3f6; }

.nav {
  list-style: none;
  border-top: 2px solid #dbe7f3;
  height: 90%;
  overflow: auto; }

.nav-item {
  display: block;
  color: #21262c;
  font-size: 0.875rem;
  position: relative;
  padding: 13px 40px; }

.side-nav__headers {
  color: #424e5a;
  padding: 15px 20px;
  display: block; }

.nav-item--selected {
  border-left: 6px solid #2fd0c8; }

.nav-item:hover,
.nav-item--active {
  color: #424e5a;
  border-left: 6px solid #2fd0c8;
  padding-left: 34px; }

.content {
  flex-grow: 1;
  overflow-y: scroll;
  padding-bottom: 2rem;
  background-color: white; }

.content-margin {
  margin-right: auto;
  margin-left: auto;
  padding-left: 1rem;
  padding-right: 1rem; }

.property-header {
  text-align: left;
  padding-right: 15px; }

.header {
  margin: 12px 0; }

