@import "./../settings";
@import "./../mixins";
@import "./../functions";

/*!
.fd-status-label+(--available, --away, --busy, --offline, --success, --warning, --error)
*/

$block: #{$fd-namespace}-status-label;

.#{$block} {

    //LOCAL VARS (set all themeable properties, always include !default)
    $fd-status-lable-icon-size: fd-space(4);
    $fd-status-indicator-available: "data:image/svg+xml;base64," !default;
    $fd-status-indicator-away:"data:image/svg+xml;base64," !default;
    $fd-status-indicator-busy:"data:image/svg+xml;base64," !default;
    $fd-status-indicator-offline:"data:image/svg+xml;base64," !default;

    @mixin fd-status-icon-bg {
        width: $fd-status-lable-icon-size;
        height: $fd-status-lable-icon-size;
        display: inline-block;
        content: "";
        line-height: 1;
        border-radius: 50%;
        position: absolute;
        top: 1px;
        left: 0;
        z-index: 1;
    }

    @mixin fd-status-icon{
        position: absolute;
        z-index: 2;
        background-repeat: no-repeat;
        content: "";
        line-height: 1;
    }

    //BLOCK BASE *******************************************
    position: relative;
    @include fd-reset;
    color: fd-color("text", 2);

    &::before{
        margin-right: fd-space(1);
        vertical-align: -8%;
    }

    //BLOCK MODIFIERS ************
    &--available,
    &--away,
    &--busy,
    &--offline {
        padding-left: fd-space(5);
    }

    &--available {
        &:before{
            @include fd-status-icon;
            width: 7px;
            height: 7px;
            top: 5px;
            left: 4px;
            background-image: url($fd-status-indicator-available);
        }
        &:after{
            @include fd-status-icon-bg;
            background-color: $fd-color--success;
        }
    }

    &--away {
        &:before{
            @include fd-status-icon;
            width: 6px;
            height: 6px;
            top: 5px;
            left: 6px;
            background-image: url($fd-status-indicator-away);
        }
        &:after{
            @include fd-status-icon-bg;
            background-color: $fd-color--warning;
        }
    }

    &--busy {
        &:before{
            @include fd-status-icon;
            width: 4px;
            height: 4px;
            top: 7px;
            left: 6px;
            background-image: url($fd-status-indicator-busy);
        }
        &:after{
            @include fd-status-icon-bg;
            background-color: $fd-color--error;
        }
    }
    
    &--offline {
        &:before{
            @include fd-status-icon;
            width: 8px;
            height: 8px;
            top: 5px;
            left: 4px;
            background-image: url($fd-status-indicator-offline);
        }
        &:after{
            @include fd-status-icon-bg;
            background-color: fd-color("text", 2);
        }
    }

    &--success {
        color: $fd-color--success;
    }

    &--warning {
        color: $fd-color--warning;
    }

    &--error {
        color: $fd-color--error;
    }

}
