@import "./../settings";
@import "./../mixins";
@import "./../functions";

/*!
.fd-input-group+(--inline)
    .fd-input-group__addon+()
        .fd-input-group__button
*/

$block: #{$fd-namespace}-input-group;
.#{$block} {
    //LOCAL VARS (set all vars used in component, always include !default)
    $fd-input-group-button-background-color: fd-color('neutral', 1);

    //BLOCK BASE *******************************************
    @include fd-reset;
    display: flex;
    vertical-align: bottom;
    // max-height: $fd-forms-height--input-text;

    //ELEMENTS *******************************************
    #{$fd-elements-inputs--text}{
        z-index: 1;
    }
    & [type="number"] {
        &::-webkit-outer-spin-button,
        &::-webkit-inner-spin-button {
            -webkit-appearance: none;
            margin: 0;
        }
        -moz-appearance: textfield;
    }
    & [type="search"] {
        background-image: url('data:image/svg+xml;base64,');
        background-repeat: no-repeat;
        background-position: fd-space(4) center;
        background-size: fd-space(4);
        padding-left: fd-space(12);
        padding-right: fd-space(15);
        &::-webkit-search-decoration {
            -webkit-appearance: none;
            margin: 0;
        }
        &::-webkit-search-cancel-button {
            -webkit-appearance: none;
            margin: 0;
        }
        -moz-appearance: textfield;
    }
    input{
        // z-index: 100;
    }
    &--inline {
        display: inline-flex;
    }
    &--no-border {
        input {
            border-color: transparent;
        }
    }
    &__addon {
        color: fd-color(text, 3);
        padding: 0 fd-space(3);
        border-style: solid;
        border-width: 1px;
        border-color: $fd-forms-border-color;
        background-color: fd-color(neutral, 1);
        justify-content: center;
        display: flex;
        flex-direction: column;
        border-radius: $fd-border-radius;
        height: $fd-forms-height--input-text;
        z-index: 2; // Brings focus state above input

        //ELEMENT MODIFIERS ************
        &:first-child {
            border-right-width: 0;
        }
        &:last-child {
            border-left-width: 0;
        }
        &--button {
            padding: 0;
        }
        &--before {
            border-top-right-radius: 0;
            border-bottom-right-radius: 0;
        }
        &--after {
            border-top-left-radius: 0;
            border-bottom-left-radius: 0;
        }

        &--textarea{
            height: auto;
        }
        @at-root {
            [readonly] + & {
                border-top-color: transparent;
                border-right-color: transparent;
                border-radius: 0;
            }
        }
        @at-root {
            [type="search"] + & {
                border: 0;
                width: 0;
            }
        }
    }
    &--before {
        #{$fd-elements-inputs--text}{
            border-top-left-radius: 0;
            border-bottom-left-radius: 0;
        }
    }
    &--after {
        #{$fd-elements-inputs--text} {
            border-top-right-radius: 0;
            border-bottom-right-radius: 0;
        }
    }
    &__button {
        @include fd-button-reset;
        flex: 1;
        width: 100%;
        display: block;
        min-width: fd-space(12);
        background: $fd-input-group-button-background-color;

        &--step-up,
        &--step-down  {
            @include fd-type("0");
            color: $fd-link-color;
            position: relative;
            &::before{
                position: absolute;
                transform: translateX(-50%);
                top: -2px;
            }
        }
        &--step-up {
            border-bottom: solid 1px $fd-forms-border-color;
            border-bottom-left-radius: 0;
            border-bottom-right-radius: 0;
        }
        &--step-down  {
            border-top: solid 1px $fd-forms-border-color;
            border-top-left-radius: 0;
            border-top-right-radius: 0;
        }
        &--clear {
            background-color: transparent;
            background-image: url('data:image/svg+xml;base64,');
            background-repeat: no-repeat;
            background-position: center;
            background-size: fd-space(5);
            position: relative;
            left: -(fd-space(11));
        }
    }
}
